body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

}
.free-space-right{
  border-bottom: 3px solid #666666;
  min-height: 25px;
  flex-grow: 1;
}


.free-space-left{
  border-bottom: 3px solid #666666;
  min-height: 25px;
  width: 15px;
}
.header-list{
  display:inline-block;
  cursor:pointer;
  color:#666666;
  font-size:20px;
  font-weight:bold;
  padding:6px 24px;
  text-decoration:none;
  float: left;
  position: relative;
  text-align: center;
  min-height: 25px;
  min-width: 100px;
  border-bottom: 3px solid  #666666;
}
.head-class{
  display: flex;
  flex-wrap: wrap;
  margin-top: 5px;
}
.header-list a{
  text-decoration: none;
  width: 100px;
  min-height: 50px;
}
.header-list:hover {
  -webkit-box-shadow: 0px -1px 100px 100px rgba(144, 144, 144, 0.2) inset;
  -moz-box-shadow: 0px -1px 100px 100px rgba(144, 144, 144, 0.2) inset;
  box-shadow: 0px -1px 100px 100px rgba(144, 144, 144, 0.2) inset;
}

.container {
  max-width: 1200px;
  padding: 0 15px;
  margin: 0 auto;
}
.container hr{
  max-width: 1000px;
  padding: 0 15px;
  margin: 0 auto;
}
.container table{
  max-width: 1000px;
  padding: 0 15px;
  margin: 0 auto;
  text-align: left;
  font-size: larger;
}
.table-top{
  border-bottom: 2px solid silver;
  border-top:2px solid silver;
}